
import './index.scss'
import { Select,Input,Button,message } from 'antd';
const { Option } = Select;
const { TextArea } = Input;
import  {connect} from 'react-redux'
import {axios,history} from '&'
import { getkj } from '../../actions'
var style={
    first:{
        fontWeight:'bold',
        marginBottom:7
    },
    second:{
        marginBottom:20
    }
}
import {Toast} from 'antd-mobile'

export function showToastNoMask(msg) {
    Toast.hide();
    Toast.info(msg, 2, null, false);
  }

@connect(
    state=>{
        return{
            user:state.getIn(['user','user']),
            kj:state.getIn(['data','kj'])
        }
    }
)
export default class Insertscore extends Component{
    constructor(props){
        super(props)
        this.state={
            three:'请选择框架类型',
            four:'',
            five:''
        }
    }
      onChangethree=(value) => {
        console.log(value)
        this.setState({ 
            three:value
         });
      };
      onChangefour=({ target: { value } }) => {
        this.setState({ 
            four:value
         });
      };
    
    onChangefive=({ target: { value } }) => {
        this.setState({ 
            five:value
         });
      };
    clear=()=>{
        this.setState({
            three:'请选择框架类型',
            four:'',
            five:''
        })
        this.one.state.value=''
        this.two.state.value=''
    }

    submit=()=>{
        if(this.one.state.value==''||this.two.state.value==''||this.state.three==''||this.state.three=='请选择框架类型'||this.state.four==""||this.state.five==""){
            message.info('请完善信息后提交')
        }else{
            var date=Date.now()
            var username=this.props.user.username
            var uid=this.props.user._id
            var mintor=this.props.user.mintor
            axios.post('/react/insertscore',{
                projectUrl:this.one.state.value,
                projectName:this.two.state.value,
                type:this.state.three,
                readme:this.state.four,
                tech:this.state.five,
                username,
                uid,
                mintor,
                date,
            }).then(res=>{
                history.push('/home/showpro')
                showToastNoMask('项目提交成功')
            })
        }
    }
    componentDidMount() {
        this.props.dispatch(getkj({url:'/react/getkj'}))
    }
    render(){
        var {
            three,
            four,
            five
        }=this.state
        return(
            <div> 
                <h2 style={style.first}>提交项目信息</h2>
                <div style={style.second}>
                    <div style={style.first}>项目上线地址：</div>
                    <Input placeholder="项目上线地址" allowClear style={{width:400}} ref={el=>this.one=el}/>
                </div>
                <div style={style.second}>
                    <div style={style.first}>项目名称：</div>
                    <Input placeholder="项目名称" allowClear style={{width:400}} ref={el=>this.two=el}/>
                </div>
                <div style={style.second}>
                    <div style={style.first}>框架：</div>
                    <Select style={{ width: 400 }}  onChange={this.onChangethree}  value={three}>
                        {
                            this.props.kj.map((item,i)=>{
                                return (
                                    <Option value={item.p_code} key={i}>{item.value}</Option>
                                )
                            })
                        }
                    </Select>
                </div>
                <div style={style.second}>
                    <div style={style.first}>技术亮点：</div>
                    <TextArea
                    value={four}
                    onChange={this.onChangefour}
                    style={{ width: 400 }}
                    placeholder="请至少填写三条技术亮点"
                    autosize={{ minRows: 4, maxRows: 7 }}
                    />
                </div>
                <div style={style.second}>
                    <div style={style.first}>备注：</div>
                    <TextArea
                    value={five}
                    onChange={this.onChangefive}
                    placeholder="请输入注册号的账号以及其他备注"
                    autosize={{ minRows: 4, maxRows: 7 }}
                    style={{ width: 400 }}
                    />
                </div>
                <Button type='primary' style={{marginRight:50}} onClick={this.submit}>提交</Button>
                <Button type='danger' onClick={this.clear}>重置</Button>
            </div>
        )
    }
}